import React from 'react'
import MapStyles from './MapStyles'
import Script from 'react-load-script'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'

require('./index.css')

export default class GMap extends React.Component {
  get basicSettings () {
    return {
      initialCenter: {
        lat: 40.4801,
        lng: 71.7267
      },
      initialZoom: 12,
    }
  }

  get autocenterSettings () {
    let settings = this.basicSettings
    settings.snapToUserLocation = true
    return settings
  }

  get brandedWithMarkerSettings () {
    let settings = this.basicSettings
    settings.colors = {
      base: '#212121',
      baseContour1: '#4d4d4d',
      baseContour2: '#797979',
      baseContour3: '#a6a6a6',
      accent: '#fcbd40',
      accentLight: '#fcb24b',
    }
    settings.icons = {
      revelry: {
        name: 'Revelry',
        image: '../img/cakeLabLogo.png',
      },
    }
    settings.markers = [
      {
        position: {
          lat: 29.975588,
          lng: -90.102682,
        },
        icon: 'revelry',
        message: [
          '<h3>Revelry Labs</h3>',
          '<p>4200 Canal St, Suite E</p>',
          '<p>New Orleans, LA 70119</p>'].join(''),
      },
    ]
    return settings
  }

  get multipleMarkersSettings () {
    let settings = this.basicSettings
    settings.initialZoom = 14
    settings.markers = [
      {
        position: {
          lat: 41.311081,
          lng: 69.240562,
        },
        message: 'One.',
      },
      {
        position: {
          lat: 29.975588,
          lng: -90.106999,
        },
        message: 'Two.',
      },
      {
        position: {
          lat: 29.977999,
          lng: -90.106999,
        },
        message: 'Three.',
      },
    ]
    return settings
  }

  get mapWithLegendSettings () {
    let settings = this.multipleMarkersSettings
    settings.icons = {
      sunglasses: {
        name: 'Sunglasses',
        image: './app/images/one.png',
      },
      burger: {
        name: 'Burger',
        image: './app/images/two.png',
      },
      smile: {
        name: 'Smile',
        image: './app/images/three.png',
      },
    }
    settings.markers[0].icon = 'sunglasses'
    settings.markers[1].icon = 'burger'
    settings.markers[2].icon = 'smile'
    settings.legend = true
    return settings
  }

  static get propTypes () {
    return {
      config: PropTypes.shape({
        colors: PropTypes.objectOf(PropTypes.string),
        icons: PropTypes.objectOf(PropTypes.objectOf(PropTypes.string)),
        initialCenter: PropTypes.objectOf(PropTypes.number),
        initialZoom: PropTypes.number,
        legend: PropTypes.bool,
        markers: PropTypes.arrayOf(PropTypes.shape({
          position: PropTypes.objectOf(PropTypes.number),
          icon: PropTypes.string,
          message: PropTypes.string,
        })),
        snapToUserLocation: PropTypes.bool,
      }),
    }
  }

  static get defaultProps () {
    return {
      config: {
        initialCenter: {
          lat: 41.311081,
          lng: 69.240562,
        },
        initialZoom: 13,
        colors: {
          base: '',
          baseContour1: '',
          baseContour2: '',
          baseContour3: '',
          accent: '',
          accentLight: '',
        },
        // icons: {
        //   revelry: {
        //     name: 'Revelry',
        //     image: './RevMarker.png',
        //   },
        // },
        markers: [
          {
            position: {
              lat: 41.3243412,
              lng: 69.2535975,
            },
            icon: './faviconoriginal.ico',
            message: [
              '<h3>CakeLab (м. Буюк Ипак йули)</h3>',
              '<p> М. Улугбекский р-н, ул. М. Улугбек, 51.</p>',
              '<p>(Ориентир – М. Улугбекский РОВД, напротив Капиталбанка) </p>'].join(''),
          },
          {
            position: {
              lat: 41.2863863,
              lng: 69.1827864,
            },
            message: [
              '<h3>CakeLab (парк им. Бабура)</h3>',
              '<p> Яккасарайский р-н, ул. Бабура, 40а.</p>',
              '<p>(Ориентир – напротив парка Бабур)</p>'].join(''),
          },
          {
            position: {
              lat: 41.2960426,
              lng: 69.1995359,
            },
            message: [
              '<h3>CakeLab (гостиница Гранд Мир)</h3>',
              '<p> Мирабадский р-н, ул. Шахрисабзская, 29/7.</p>',
              '<p>(Ориентир – напротив гостиницы Гранд Мир (бывш. Россия))</p>'].join(''),
          },
          {
            position: {
              lat: 41.2966039,
              lng: 69.2092002,
            },
            message: [
              '<h3>CakeLab (Перфектум, школа № 60)</h3>',
              '<p>Мирабадский р-н, ул. Т. Шевченко, 21.</p>',
              '<p>(Ориентир – Перфектум, школа № 60)</p>'].join(''),
          },

        ]
      },
    }
  }

  constructor (props) {
    super(props)
    this.state = {
      center: null,
    }
  }

  loadMap () {
    const {config} = this.props
    if (this.state.scriptLoaded) {
      if (config && config.snapToUserLocation && navigator.geolocation) {
        this.getUserLocation()
      } else {
        this.setState({
          center: this.mapCenter(config.initialCenter.lat, config.initialCenter.lng),
        })
      }
      // create the map and markers after the component has
      // been rendered because we need to manipulate the DOM for Google =(
      this.map = this.createMap(config.initialCenter)
      if (config && config.markers) {
        this.markers = this.createMarkers(config.markers)
        if (config.legend) {
          this.createLegend(config.icons)
        }
      }
    }
  }

  // clean up event listeners when component unmounts
  componentDidUnMount () {
    google.maps.event.clearListeners(map, 'click')
  }

  createLegend (icons) {
    const {legend} = this.refs
    for (const key in icons) {
      const type = icons[key], name = type.name, icon = type.image
      const div = document.createElement('div')
      div.innerHTML = `<img src="${icon}"> ${name}`
      legend.appendChild(div)
    }
    this.map.controls[google.maps.ControlPosition.RIGHT_BOTTOM].push(legend)
  }

  createMap (center) {
    const {config} = this.props
    const mapOptions = {
      zoom: this.props.config.initialZoom,
      center: center,
    }
    if (config && config.colors) {
      mapOptions.styles = MapStyles(config.colors)
      mapOptions.mapTypeId = 'terrain'
    }
    return new google.maps.Map(this.refs.mapCanvas, mapOptions)
  }

  createMarkers (markers) {
    const markersArray = markers.map((marker) => {
      const {config} = this.props,
        icon = config.icons && config.icons[marker.icon].image,
        thisMarker = this.newMarker(marker.position, icon)

      // have to define google maps event listeners here too
      // because we can't add listeners on the map until it's created
      if (marker.message) {
        thisMarker.infoWindowIsOpen = false
        google.maps.event.addListener(thisMarker, 'click', () => this.handleMarkerClick(thisMarker, marker.message))
      }
      return thisMarker
    })
    return markersArray
  }

  getUserLocation () {
    // lets map autocenter on user's location (if the user enables it)
    // which takes a while, so the map should get rendered with the initial center first
    navigator.geolocation.getCurrentPosition((position) => {
      this.moveMap(position.coords.latitude, position.coords.longitude, 'You are here.')
    }, () => alert("Couldn't find your location"))
  }

  handleMarkerClick (marker, message) {
    if (!marker.infoWindowIsOpen) {
      marker.infoWindowIsOpen = true
      this.newInfoWindow(marker, message)
    } else {
      marker.infoWindowIsOpen = false
      marker.infoWindow.close()
    }
  }

  handleScriptCreate () {
    this.setState({
      scriptLoaded: false,
    })
  }

  handleScriptError () {
    this.setState({
      scriptError: true,
    })
  }

  handleScriptLoad () {
    this.setState({
      scriptLoaded: true,
    })
    this.loadMap()
  }

  newInfoWindow (anchor, content) {
    anchor.infoWindow = new google.maps.InfoWindow({
      map: this.map,
      anchor: anchor,
      content: content,
    })
    google.maps.event.addListenerOnce(anchor.infoWindow, 'closeclick', () => anchor.infoWindowIsOpen = false)
    return anchor.infoWindow
  }

  newMarker (position, image) {
    return new google.maps.Marker({
      position: position,
      map: this.map,
      draggable: true,
      animation: google.maps.Animation.DROP,
      icon: image,
    })
  }

  mapCenter (lat, lng) {
    return new google.maps.LatLng(lat, lng)
  }

  moveMap (lat, lng, message) {
    this.setState({
      center: this.mapCenter(lat, lng),
    })
    this.map.panTo(this.state.center)
    let thisMarker = this.newMarker(this.state.center)
    this.newInfoWindow(thisMarker, message)
  }

  render () {
    let url = 'http://maps.googleapis.com/maps/api/js?key=AIzaSyDhIg5qo5GwggvDN5byCAU36nf_SFcwKUY'
    return (
      <div className='GMap' config={this.basicSettings}>
        <Script
          url={url}
          onCreate={this.handleScriptCreate.bind(this)}
          onError={this.handleScriptError.bind(this)}
          onLoad={this.handleScriptLoad.bind(this)}
        />
        <div className='GMap-canvas' ref='mapCanvas' />
        {this.props.config.legend && <div ref='legend' className='legend'><h3>Legend</h3></div>}
      </div>
    )
  }
}



